.card {
  line-height: 14px;
  text-align: left;
  width: 200px;
  border-radius: 5px;
  background-color: $main-bg-color;
  border: 1px solid #f3f3f3;
  padding: 16px;
  transition: 300ms;

  &:hover {
    transform: scale(1.2);
    cursor: pointer;
  }

  &__picture {
    display: block;
    margin: 16px 3px 40px;
    width: 160px;
    height: 134px;
    background-image: url(../images/imac.png);
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__title {
    color: $main-color;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    margin-bottom: 0;
  }

  &:hover &__title {
    color: #34568b;
  }

  &__code {
    margin-top: 4px;
    margin-bottom: 16px;
    color: $main-second-color;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  &__reviews {
    margin: 0;
    color: $main-color;
    line-height: 16px;
  }

  &__price {
    margin-top: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  &__price-label {
    margin: 0;
    display: block;
    font-size: 12px;
    line-height: 18px;
    color: $main-second-color;
  }

  &__price-value {
    margin: 0;
    display: block;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    color: $main-color;
  }

  &__button {
    margin-top: 16px;
    display: block;
    text-transform: uppercase;
    text-align: center;
    line-height: 40px;
    background-color: $button-color;
    color: $main-bg-color;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 700;
    text-decoration: none;
  }

  &__button:hover {
    background-color: $main-bg-color;
    color: $button-color;
    border: 1px solid $button-color;
  }
}
